import React, { useEffect, useRef, useState } from "react";
import { connect } from "react-redux";
import {
  setLightness,
  setSaturation,
  setSelectorCount,
  resetState,
  setLinkedState,
  setSelectorAngle,
  setSelectorRadius,
  setSelectorStagger,
  setDarkMode
} from "../../../redux/actions/actions";
import "./Controls.scss";
import "../../Slider/Slider.scss";

const Controls_ = props => {
  const staggerSlider = useRef(null);
  const radiusSlider = useRef(null);

  useEffect(() => {
    props.setSelectorStagger(staggerSlider.current.value);
  }, [props.selectorRadius]);

  useEffect(() => {
    props.setSelectorRadius(radiusSlider.current.value);
  }, [props.selectorAngle]);

  return (
    <div className={`controls ui-half-block ${props.darkMode ? "dark" : ""}`}>
      <div className="controls-wrapper">
        <div className="slider-container">
          <p className="slider-label">
            Number of Colors:{" "}
            <span className="label-value">{props.selectorCount}</span>
          </p>
          <input
            type="range"
            className={`slider ${props.darkMode ? "dark" : ""}`}
            min={props.preset}
            max={15}
            step={props.preset}
            value={props.selectorCount}
            onChange={val => {
              props.setSelectorCount(parseInt(val.target.value));
            }}
          />
        </div>
        <div className="slider-container">
          <p className="slider-label">
            Saturation: <span className="label-value">{props.saturation}%</span>
          </p>
          <input
            type="range"
            className={`slider ${props.darkMode ? "dark" : ""}`}
            min={10}
            max={100}
            step={1}
            value={props.saturation}
            onChange={val => {
              props.setSaturation(parseInt(val.target.value));
            }}
          />
        </div>
        <div className="slider-container">
          <p className="slider-label">
            Lightness: <span className="label-value">{props.lightness}%</span>
          </p>
          <input
            type="range"
            className={`slider ${props.darkMode ? "dark" : ""}`}
            min={10}
            max={90}
            step={1}
            value={props.lightness}
            onChange={val => {
              props.setLightness(parseInt(val.target.value));
            }}
          />
        </div>
        <div className="slider-container">
          <p className="slider-label">
            Hue Offset:{" "}
            <span className="label-value">
              {(
                Math.round((props.selectorAngle / props.selectorCount) * 10) /
                10
              ).toFixed(1)}
              
            </span>
          </p>

          <input
            type="range"
            className={`slider ${props.darkMode ? "dark" : ""}`}
            min={10}
            max={360}
            step={1}
            value={props.selectorAngle}
            onChange={val => {
              props.setSelectorAngle(parseInt(val.target.value));
            }}
          />
        </div>
        <div className="slider-container">
          <p className="slider-label">
            Distance:{" "}
            <span className="label-value">
              {(
                Math.round(
                  (props.selectorLinkedRadius / (props.viewport * 0.325)) * 1000
                ) / 10
              ).toFixed(0)}
            </span>
          </p>
          <input
            type="range"
            className={`slider ${props.darkMode ? "dark" : ""}`}
            ref={radiusSlider}
            min={1}
            max={0.325 * props.viewport}
            step={1}
            value={props.selectorLinkedRadius}
            onChange={val => {
              props.setSelectorRadius(parseInt(val.target.value));
            }}
          />
        </div>
        <div className="slider-container">
          <p className="slider-label">
            Stagger:{" "}
            <span className="label-value">
              {(Math.round(props.selectorStagger * 10) / 10).toFixed(0)}
            </span>
          </p>
          <input
            type="range"
            className={`slider ${props.darkMode ? "dark" : ""}`}
            ref={staggerSlider}
            min={-(0.315 * props.viewport - props.selectorRadius)}
            max={props.selectorRadius}
            step={1}
            value={props.selectorStagger}
            onChange={val => {
              props.setSelectorStagger(parseInt(val.target.value));
            }}
          />
        </div>
      </div>
    </div>
  );
};

function mapStateToProps(state) {
  return {
    lightness: state.actionReducer.LIGHTNESS,
    saturation: state.actionReducer.SATURATION,
    selectorCount: state.actionReducer.SELECTOR_COUNT,
    selectorAngle: state.actionReducer.SELECTOR_ANGLE,
    selectorRadius: state.actionReducer.SELECTOR_RADIUS,
    selectorLinkedRadius: state.actionReducer.SELECTOR_LINKED_RADIUS,
    selectorStagger: state.actionReducer.SELECTOR_STAGGER,
    darkMode: state.actionReducer.DARK_MODE,
    preset: state.actionReducer.PRESET,
    viewport: state.actionReducer.VIEWPORT_HEIGHT
  };
}

const mapDispatchToProps = {
  setLightness,
  setSaturation,
  setSelectorCount,
  resetState,
  setLinkedState,
  setSelectorAngle,
  setSelectorRadius,
  setSelectorStagger,
  setDarkMode
};

const Controls = connect(mapStateToProps, mapDispatchToProps)(Controls_);

export default Controls;
